<template>
	<transition name="fade">
		<div class="detail" v-show="show">
			<div class="detail-hd">
				<img :src="food.image" alt="">
				<div class="back" @click="show=false">
					<i class="iconfont icon-weibiaoti6-copy"></i>
				</div>
			</div>
			<div class="detail-bd">
				<div class="content">
					<h2 class="title">{{food.name}}</h2>
					<p class="sale">
						<span class="sell-count">月售{{food.sellCount}}</span>
						<span class="rating">好评率{{food.rating}}</span>
					</p>
					<p class="price">
						<strong class="now">￥{{food.price}}</strong>
						<span v-if="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
					</p>
					<!-- 添加到购物车按钮 -->
					<div class="cartControl-wrapper">
						<!-- 添加购物车组件 -->
						<ele-buy :food="food"></ele-buy>
					</div>
					<div class="buy">加入购物车</div>
					<!--  -->
				</div>
				<div class="split"></div>
				<div class="info">
					<h1 class="title">商品信息</h1>
					<p class="text">{{food.info}}</p>
				</div>
				<div class="split"></div>
			</div>
		</div>
	</transition>
</template>

<script>
// 引入buy组件
	import Buy from '../Buy/Buy.vue';
	export default{
		props:{
			food:{
				type:Object
			}
		},
		data(){
			return {
				show:false
			}
		},
		methods:{
			showFood(){
				this.show = true;
			}
		},
		components:{
			// 注册组件
			"ele-buy":Buy
		}
	}
</script>
<style lang="less" scoped>
	.fade-enter-active,.fade-leave-active{
		transition:all 0.4s;
	}
	.fade-enter,.fade-leave-active{
		transform:translate(100%);
		opacity: 0;
	}
	.detail{
		position: fixed;
		width: 100%;
		top:0;
		left:0;
		bottom:0;
		background: #fff;
		.detail-hd{
			img{
				width: 100%;
				height: 100%;
			}
			.back{
				position: absolute;
				
				top:10px;
				color:#fff;
				font-size: 32px;
				.iconfont{
					display:block;
					padding: 10px;
					font-size: 20px;
					color:#fff;
				}
			}
		}
		.detail-bd{

			.content{
				position: relative;
				padding:18px;
				.title{
					line-height: 14px;
					margin-bottom: 8px;
					font-size: 14px;
					font-weight: 700;
					color: #07111b;
				}
				.sale{
					margin-top:18px;
					line-height: 10px;
					// font-size: 0;
					height: 10px;
					span{
						margin-right: 12px;
						font-size: 10px;
						display:inline-block;
						color: #93999f;
					}
					.sell-count{

					}
				}
				.price{
					font-weight: 700px;
					line-height: 24px;
					.now{
						margin-right:8px;
						font-size: 14px;
						color:#f01414;
					}
				}
				.cartControl-wrapper{
					position: absolute;
				    right: 12px;
				    bottom: 0px;
				}
				.buy{
					position: absolute;
					right: 12px;
					bottom: 10px;
					height: 24px;
					line-height: 24px;
					padding:0 12px;
					z-index: 10;
					padding:0 12px;
					box-sizing: border-box;
					border-radius:12px;
					font-size: 10px;
					color:#fff;
					background: #00a0dc;
				}
			}
			.split{
				width: 100%;
				height: 16px;
				border-top:1px solid rgba(1,17,27,0.1);
				background: #f3f5f7;
			}
			.info{
				padding:18px;
				.title{
					line-height: 14px;
				    margin-bottom: 6px;
				    font-size: 14px;
				    color: #07111b;
				}
				.text{
					font-size: 12px;
				    line-height: 24px;
				    padding: 0 8px;
				    color: #4d555d;
				}
			}
		}
		
	}
</style>


