<template>
	<div>
		<div class="goods">
			<div class="menu">
				<ul>
					<li v-for="(item,index) in goods" :class="{'on' :index==current}" @click="selectCat(index)">{{item.name}}</li>
				</ul>
			</div>
			<!-- 列表 -->
			<div class="list" id="list" ref="list">
				<div>
					<dl v-for='item in goods'>
						<dt>{{item.name}}</dt>
						<dd v-for="food in item.foods">
							<div class="food-container" @click.stop="showDetail(food)">
								<img :src="food.icon" alt="">
								<div class="food-info">
									<h3>{{food.name}}</h3>
									<p class="desc">{{food.description}}</p>
									<p class="sale"><span>月售{{food.sellCount}}</span>
										<span>好评{{food.rating}}</span></p>
									<p class="price"><strong>￥{{food.price}}</strong><del v-if="food.oldPrice">￥{{food.oldPrice}}</del></p>
								</div>
							</div>
							<!-- 添加到购物车按钮 自定义的组件-->
							<ele-buy :food="food" > </ele-buy>
						</dd>
					</dl>
				</div>
			</div>
		</div>
		<!-- 商品详情组件 -->
		<ele-detail ref="food" :food="selectedFood"></ele-detail>
		<!-- 购物车组件 -->
		<ele-cart :foods="cartFoods" :fee="seller.deliveryPrice" :start="seller.minPrice"></ele-cart>
	</div>
</template>

<script>
	import data from '../../../data.json';
	// 引入detail组件
	import Detail from '../Detail/Detail';
	//引用 添加购物车组件
	import Buy from '../Buy/Buy.vue';
	//引用 购物车组件
	import Cart from '../Cart/Cart';
	export default {
		// name:"Goods",
		props:{
			seller:{
				type:Object
			}
		},
		data(){
			return {
				goods:[],
				current:0,
				selectedFood:{}//当前选中的商品
			}
		},
		computed:{
			cartFoods(){
				let arr = [];
				this.goods.forEach( (item)=>{
					// item是指分类对象，还需要遍历
					item.foods.forEach((item2)=>{
						//item2是指每一个food
						if(item2.count){
							//push到arr中
							arr.push(item2);
						}
					});
				});
				console.log(arr);
				return arr;
			}
		},
		components:{
			// 注册组件
			"ele-detail":Detail,
			'ele-buy':Buy,
			'ele-cart':Cart
		},
		created(){
			this.goods = data.goods;
		},
		mounted(){
			this.scrollFoods = new IScroll("#list",{click:true});
			new IScroll(".menu",{click:true});
		},
		methods:{
			selectCat(index){
				// alert(1);
				//获取右边所有的dl
				let dls = this.$refs.list.getElementsByTagName('dl');
				//滚动到对应index的dl
				this.scrollFoods.scrollToElement(dls[index]);
				// 改变当前分类
				this.current = index;
			},
			//显示隐藏 商品详细
			showDetail(food){
				// 调用子组件对象的showfood方法
				this.$refs.food.showFood();
				//设置selectedFood的值
				this.selectedFood = food;
			}
		}
	}
</script>

<style lang="less" scoped>
	.goods{
		display: flex;
		position: absolute;
		left:0;
		right:0;
		top:174px;
		bottom:48px;
		overflow:hidden;
		.menu{
			flex-basis:80px;
			min-width: 80px;
			li{
				height: 54px;
				display:flex;
				align-items:center;
				font-size: 12px;
				background: #f3f5f7;
				color:#4d555d;
				padding: 0 12px;
				&.on{
					color:#4d555d;
					background: #fff;
				}
			}
		}
		.list{
			flex-grow:1;
			dt{
				height: 26px;
				line-height: 26px;
				background: #f3f5f7;
				font-size: 12px;
				color:#93999f;
				border-left:2px solid #d9dde1;
				padding-left:14px;
			}
			dd{
				margin:18px;
				padding-bottom:10px;
				border-bottom:1px solid rgba(7, 17, 27, 0.1);
				position: relative;
			}
			.food-container{
				display:flex;
				img{
					width: 57px;
					height: 57px;
					margin-right:10px;
				}
				.food-info{
					flex-grow:1;
					h3{
						font-size: 14px;
						color:#07111b;
						line-height: 14px;
					}
					.desc{
						font-size: 10px;
						color:#93999f;
						margin:8px 0;
					}
					.sale{
						font-size: 10px;
						color:#93999f;
						margin:8px 0;
					}
					.price strong{
						color:#f01414;
						font-size: 14px;
						font-weight: bold;
					}
				}
			}
		}
	}
</style>


