<template>
	<div class="buy-container">
		<transition name="rotate">
			<span class="iconfont icon-jian decrease" @click.stop="decrease" v-show="food.count"></span>
		</transition>
			<span class="count" v-show="food.count">{{food.count}}</span>
			<span class="iconfont icon-jia increase" @click.stop="increase"></span>
	</div>
</template>

<script>
	export default {
		props:{
			food:{
				type :Object
			}
		},
		methods : {
			//减少
			decrease(){
				// this.food表示当前这个商品
				this.food.count--;
				// console.log(this.food.count);
			},
			//增加
			increase(){
				//对food.count进行判断，如果没有就设置为1 ，如果有则++
				if(!this.food.count){
					this.$set(this.food,'count',1);
				}else{
					this.food.count++;
				}
				// console.log(this.food.count);

			}
		}
	}
</script>

<style lang="less" scoped>
	.rotate-enter-active ,.rotate-leave-active{
		transition:all 0.3s;
	}
	.rotate-enter, .rotate-leave-active{
		transform : translateX(24px) rotate(360deg);
	}
	.buy-container{
		position: absolute;
		right: 0px;
		bottom: 10px;
		display:flex;
		align-items:center;
		.count{
			color:#999;
			margin:0 8px;
		}
		.decrease,.increase{
			color:#00a0bc;
			font-size: 24px;
		}
		.decrease{
			position: relative;
			top: -2px;
		}
	}
</style>


