.nav{
  height: 40px;
  display: flex;
  justify-content:space-around;
  align-items:center;
  border-bottom:1px solid rgba(7,17,27,.1);
  a{
    text-decoration: none;
    font-size: 14px;
    color:rgb(77,85,93);
    &.router-link-active{
      color:rgb(240,20,20);
    }
  }


}
</style>



